import { Component } from '@angular/core';
import { APP_ROUTE } from '@core/constants';
import { PremiumUnlimitedPlan } from '@core/enums';
import { WindowService } from '@core/services/window.service';
import { environment } from '@environments/environment';
import { SubscriptionOffersComponent } from '@shared/components/subscription-offers/subscription-offers.component';
import { MyProfileFacade } from '@feature/my-profile/my-profile.facade';
import { SubscriptionButtonComponent } from '@shared/components/subscription-button/subscription-button.component';
import { SharedModule } from '@shared/shared.module';
import { finalize } from 'rxjs';

@Component({
    selector: 'findqo-payment-plan',
    templateUrl: './payment-plan.component.html',
    styleUrl: './payment-plan.component.scss',
    imports: [SharedModule, SubscriptionOffersComponent, SubscriptionButtonComponent]
})
export class PaymentPlanComponent {
	constructor(
		private readonly profileFacade: MyProfileFacade,
		private readonly windowService: WindowService
	) {}

	paymentButtonLoaders: { [key: string]: boolean } = {};

	readonly paymentPlans = [PremiumUnlimitedPlan.MONTHLY, PremiumUnlimitedPlan.YEARLY];

	protected generatePaymentLink(paymentPlan: PremiumUnlimitedPlan, planTierId: string): void {
		this.paymentButtonLoaders[paymentPlan] = true;
		this.profileFacade
			.generatePaymentLink({ planTierId, successUrl: `${environment.appUrl}/${APP_ROUTE.agentHub.dashboard}/${APP_ROUTE.businessProfile.paymentSuccess}` })
			.pipe(finalize(() => (this.paymentButtonLoaders[paymentPlan] = false)))
			.subscribe((res) => this.windowService.reference.location.replace(res.url));
	}
}
